import styled from "styled-components";
import { useEffect, useRef } from "react";
import { useState } from "react";

import { Title2 } from "../../assets/styles/component";
import VerticalBox from "./verticalBoxMobile";

import { YouTube } from "../../assets/images/Socials/Youtube";
import { Odnoklassniki } from "../../assets/images/Socials/Odnoklassniki";
import { Vk } from "../../assets/images/Socials/Vk";
import { Telegram } from "../../assets/images/Socials/Telegram";
import { Instagram } from "../../assets/images/Socials/Instagram";
import { YandexZen } from "../../assets/images/Socials/YandexZen";

import RocketVideo from "../../assets/images/Products/RocketVideo.svg";
import SocialData from "../../assets/images/Products/SocialData.svg";
import TeleJet from "../../assets/images/Products/TeleJet.svg";
import SocialLead from "../../assets/images/Products/SocialLead.svg";
import SocialStream from "../../assets/images/Products/SocialStream.svg";

import { EngLoc } from "../../assets/constants/localizations";

const SProducts = styled.section`
  display: flex;
  flex-direction: column;
  gap: 2.188rem;
  align-items: center;
  text-align: left;

  margin: 0 1rem;

  @media (min-width: 600px) {
    margin: 0 4%;
  }

  padding-bottom: 3.75rem;

  & > div {
    opacity: 0;
  }
`;

const HightTitle = styled(Title2)`
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-align: left;
  width: 100%;
  font-weight: 400;
  font-size: 2.375rem;
  line-height: 110%;
`;

const Products = ({ language }) => {
  return (
    <SProducts>
      <HightTitle>
        {language === "Ru" ? "Наши продукты" : EngLoc.products.title}
      </HightTitle>
      <VerticalBox
        language={language}
        boxcolor="blue"
        btncolor="#2E86F7"
        title={
          language === "Ru"
            ? "Крупнейшая аналитическая и рекламная платформа для маркетинга и бизнеса"
            : EngLoc.products.products_boxes[0].title
        }
        name="SocialData"
        view={SocialData}
        list={[
          language === "Ru"
            ? "Более 500 тыс. источников для размещения рекламы Аналитика по десяткам параметров."
            : EngLoc.products.products_boxes[0].desc_list[0],
          language === "Ru"
            ? "Запросы на подборки и готовые подборки"
            : EngLoc.products.products_boxes[0].desc_list[2],
          language === "Ru"
            ? "Запуск рекламных кампаний в несколько кликов"
            : EngLoc.products.products_boxes[0].desc_list[1],
          language === "Ru"
            ? "Автоматизация рутинных процессов: размещения, статистика и отчетность, оплаты"
            : EngLoc.products.products_boxes[0].desc_list[3],
        ]}
        video={"https://www.youtube.com/embed/9MB7C29ibtY"}
        socials={[
          <Telegram key={1} />,
          <Vk key={2} />,
          <Instagram key={3} />,
          <Odnoklassniki key={4} />,
          <YandexZen key={5} />,
          <YouTube key={6} />,
        ]}
        href="https://data.socialjet.pro/"
      />
      <VerticalBox
        name="TeleJet"
        view={TeleJet}
        language={language}
        boxcolor="green"
        btncolor="#1BB8E8"
        title={
          language === "Ru"
            ? "Крупнейшая аналитическая и рекламная платформа для маркетинга и бизнеса"
            : EngLoc.products.products_boxes[1].title
        }
        list={[
          language === "Ru"
            ? "Автоматизация размещений с оплатой по CPM, CPC, CPF"
            : EngLoc.products.products_boxes[1].desc_list[0],
          language === "Ru"
            ? "Прозрачная статистика и оптимизация на основе данных"
            : EngLoc.products.products_boxes[1].desc_list[1],
          language === "Ru"
            ? "Запуск РК в несколько кликов"
            : EngLoc.products.products_boxes[1].desc_list[2],
          language === "Ru"
            ? "Автоматизация рутинных процессов"
            : EngLoc.products.products_boxes[1].desc_list[3],
        ]}
        video={""}
        socials={[<Telegram key={1} />]}
        href="https://socialjet.pro/telegram/"
      />
      <VerticalBox
        name="RocketVideo"
        view={RocketVideo}
        language={language}
        boxcolor="pink"
        btncolor="#713160"
        title={
          language === "Ru"
            ? "Платформа для размещения видео-рекламы на контентных и кино- сайтах"
            : EngLoc.products.products_boxes[2].title
        }
        list={[
          language === "Ru"
            ? "Доступный инвентарь более 70 млн. просмотров видео ежедневно worldwide"
            : EngLoc.products.products_boxes[2].desc_list[0],
          language === "Ru"
            ? "Глубокая аналитика по более 10 параметрам кликовБолее 30 параметров таргетинга"
            : EngLoc.products.products_boxes[2].desc_list[1],
          "Удобный личный кабинет с подробной статистикой и отчетностью",
          "Оптимизация размещений на основе данных",
        ]}
        video={""}
        socials={[]}
        href="https://rocketvideo.socialjet.pro/"
      />
      <VerticalBox
        name="SocialLead"
        view={SocialLead}
        language={language}
        boxcolor="darkblue"
        btncolor="#2E1584"
        title={"Платформа для получения конверсий с авторов и блогеров"}
        list={[
          "Глубокая аналитика по более 15 параметрам",
          "Запросы на подборки и готовые подборки",
          "Запуск РК в несколько кликов",
          "Автоматизация рутинных процессов: размещения, статистика и отчетность, оплаты",
        ]}
        video={""}
        socials={[
          <Telegram key={1} />,
          <Vk key={2} />,
          <Instagram key={3} />,
          <Odnoklassniki key={4} />,
          <YandexZen key={5} />,
          <YouTube key={6} />,
        ]}
        href="https://socialjet.pro/sociallead"
      />
      <VerticalBox
        name="SocialStream"
        view={SocialStream}
        language={language}
        boxcolor="darkviolet"
        btncolor="#1F1F1F"
        title={
          language === "Ru"
            ? "Интеграция брендов и продуктов в стримы блогеров с помощью дополненной реальности"
            : EngLoc.products.products_boxes[3].title
        }
        list={[
          "Объединение технологии AR и AI для уникализации контента",
          "Детальная аналитика аккаунтов",
          "Умная система подбора с фильтрацией",
          "Пост аналитика рекламных кампаний",
        ]}
        video={"https://www.youtube.com/embed/9MB7C29ibtY"}
        socials={[
          <Telegram key={1} />,
          <Vk key={2} />,
          <Instagram key={3} />,
          <Odnoklassniki key={4} />,
          <YandexZen key={5} />,
          <YouTube key={6} />,
        ]}
        href="https://socialjet.pro/socialstream"
      />
    </SProducts>
  );
};

export default Products;
